import Vue from 'vue'
import Router from 'vue-router'

ym(39511350, 'init', {
    defer: true,
    clickmap:true,
    trackLinks:true,
    accurateTrackBounce:true
});

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            component: () => import( '../views/Home.vue'),
        },
        {
            path: '/about',
            name: 'about',
            // route level code-splitting
            // this generates a separate chunk (about.[hash].js) for this route
            // which is lazy-loaded when the route is visited.
            component: () => import('../views/About.vue'),
        },
        {
            path: '/contacts',
            name: 'contacts',
            component: () => import('../views/Contacts.vue'),
        },
        {
            path: '/id:id',
            name: 'catalogDetail',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Coupons.vue'),
        },
        {
            path: '/img:id',
            name: 'catalogDetailImg',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/CouponsImg.vue'),
        },
        {
            path: '/sid:sid',
            name: 'catalogList',
            props: true,
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Coupons.vue'),
        },
        {
            path: '/catalog',
            name: 'catalog',
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Coupons.vue'),
        },
        {
            path: '/search',
            name: 'search',
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Search.vue'),        },
        {
            path: '/favorites',
            name: 'favorites',
            component: () => import(/* webpackChunkName: "catalog" */ '../views/Favorites.vue'),
        },
        {
            path: '/faq',
            name: 'faq',
            component: () => import(/* webpackChunkName: "faq" */ '../views/Faq.vue'),
        },
        {
            path: '/cooperation',
            name: 'cooperation',
            component: () => import(/* webpackChunkName: "cooperation" */ '../views/Cooperation.vue'),
        },
        {
            path: '/rating',
            name: 'rating',
            component: () => import(/* webpackChunkName: "rating" */ '../views/Rating.vue'),
        },
        {
            path: '/suppliers-rating',
            name: 'suppliers-rating',
            component: () => import(/* webpackChunkName: "rating" */ '../views/SuppliersRating.vue'),
        },
        {
            path: '/personal',
            name: 'personal',
            component: () => import(/* webpackChunkName: "personal" */ '../views/Personal.vue'),
        },
        {
            path: '/personal/profile',
            name: 'profile',
            component: () => import(/* webpackChunkName: "personal" */ '../views/ProfilePublic.vue'),
        },
        {
            path: '/offer',
            name: 'offer',
            component: () => import(/* webpackChunkName: "offer" */ '../views/Offer.vue'),
        },
        {
            path: '/deposit',
            name: 'deposit',
            // which is lazy-loaded when the route is visited.
            component: () => import(/* webpackChunkName: "deposit" */ '../views/Deposit.vue'),
        },
        {
            path: '/statistic',
            name: 'statistic',
            component: () => import(/* webpackChunkName: "rating" */ '../views/Statistic.vue'),
        },
        {
            path: '/payment',
            name: 'payment',
            component: () => import(/* webpackChunkName: "payment" */ '../views/StatusPayment.vue'),
        },
        {
            path: '/html-partner/coupons',
            name: 'partnerCoupons',
            component: () => import(/* webpackChunkName: "payment" */ '../views/PartnerCoupons.vue'),
        },
        {
            path: '/html-moderator/claims',
            name: 'moderatorClaimsPartnership',
            component: () => import(/* webpackChunkName: "payment" */ '../views/ModeratorClaimsPartnership.vue')
        },
        {// публичка - стоимость (7)
            path: '/cost',
            name: 'cost',
            component: () => import(/* webpackChunkName: "payment" */ '../views/html-temp/Cost.vue'),

        },
        {
            path: '/push',
            name: 'push',
            component: () => import(/* webpackChunkName: "home" */ '../views/Home.vue'),
            path: '/supplier',
            name: 'supplier',
            component: () => import(/* webpackChunkName: "payment" */ '../views/Supplier.vue')
        },
        {
            path: '/new-cooperation',
            name: 'newCooperation',
            component: () => import(/* webpackChunkName: "payment" */ '../views/NewCooperation.vue')
        },
        {
            path: '/new-supplier',
            name: 'newSupplier',
            component: () => import(/* webpackChunkName: "payment" */ '../views/NewSupplier.vue')
        },
        {
            path: '*',
            component: () => import(/* webpackChunkName: "404" */ '../views/page404.vue'),
        }
    ],
    scrollBehavior (to, from, savedPosition) {
      if (savedPosition) {
        return savedPosition
      } else {
        return { x: 0, y: 0 }
      }
    }
})
