import Vue from 'vue'
import axios from 'axios'
import App from './App.vue'
import router from './routers/public'
import store from './storages/public'
import VueCookies from 'vue-cookies'
import YmapPlugin from './helpers/vue-yandex-map/index'
import Meta from 'vue-meta';
import { isMobileOnly } from 'mobile-device-detect';
import VueNumber from 'vue-number-animation'

if(isMobileOnly){
    window.location.href="https://m.zemskidki.ru" + window.location.pathname + window.location.search + window.location.hash
}

import VueInputMask from '@/helpers/vue-inputmask.js';
const optionsYMap = { // you may define your apiKey, lang and version or skip this.
    apiKey: '',
    lang: 'ru_RU',
    version: '2.1'
}

axios.interceptors.response.use(function(response) {
    if(response.data.version){
        //console.log(process.env.VUE_APP_API_VERSION);
        if (response.data.version != process.env.VUE_APP_API_VERSION && response.data.version != store.state.notifyPageVersion){
            store.dispatch('popups/open', {name: 'notifyPageReset'});
            store.commit('setNotifyPageVersion',response.data.version);
        }
    }
    return response;
}, function (error) {
    // Any status codes that falls outside the range of 2xx cause this function to trigger
    // Do something with response error
    return Promise.reject(error);
});
Vue.use(VueInputMask);
Vue.use(VueCookies);
Vue.use(YmapPlugin, optionsYMap);
Vue.use(Meta);
Vue.use(VueNumber);
import vueReactiveStorage from "vue-reactive-storage";
import qs from "qs";
Vue.use(vueReactiveStorage, {
    "token": 'false',
});
Vue.config.productionTip = false;

router.beforeEach((to, from, next) => {
    document.getElementsByTagName('body')[0].style.background  = null;
    document.getElementsByTagName('body')[0].classList.remove("page-about");
    document.getElementsByTagName('body')[0].classList.remove("page-about-clear");
    document.getElementsByTagName('body')[0].classList.remove("supplier-page");
    document.getElementsByTagName('body')[0].classList.remove("icon-bg");
    document.getElementsByTagName('body')[0].classList.remove("no-b-padding");

    switch (to.name) {
        case 'about':
            document.getElementsByTagName('body')[0].classList.add("page-about-clear");
            break;
        case 'deposit':
            document.getElementsByTagName('body')[0].classList.add("no-b-padding");
            break;
        case 'statistic':
            document.getElementsByTagName('body')[0].classList.add("page-statistic");
            break;
        case 'faq':
            document.getElementsByTagName('body')[0].classList.add("icon-bg");
            break;
        case 'newCooperation':
            document.getElementsByTagName('body')[0].classList.add("no-b-padding");
            break;
        case 'newSupplier':
            document.getElementsByTagName('body')[0].classList.add("no-b-padding");
            break;
        case 'contacts':
            document.getElementsByTagName('body')[0].classList.add("icon-bg");
            break;
        case 'cooperation':
            document.getElementsByTagName('body')[0].classList.add("icon-bg");
            break;
    }

    if(from.name == 'statistic'){
      document.getElementsByTagName('body')[0].classList.remove("page-statistic");
    }
    if(to.name === from.name && to.name === 'catalogList'){
        window.app.$store.commit('actions/setCouponList', {items: []});
        window.app.$store.commit('actions/setPage', 0);
        window.app.$store.commit('actions/previousSection', to.params.sid);
        window.app.$store.dispatch('actions/activeCategory', {
            currentSection:to.params.sid,
            isLoad: true,
        });
    }


    if(window.app.$store && to.name !== from.name && to.name === 'catalogList'){
        window.app.$store.commit('actions/setCouponList', {items: []});
        window.app.$store.commit('actions/setPage', 0);
    }
    next()
});

let app = new Vue({
    router,
    store,
    render: function (h) { return h(App) }
})
window.app = app;
app.$mount('#app')

router.beforeEach((to, from, next) => {
    let notNext = false;
    if(!(localStorage.getItem('token') !== 'false' && localStorage.getItem('token') !== null && localStorage.getItem('token') !== undefined)){
        window.app.$store.dispatch('auth/logout');
        window.app.localStorage.token = false;
        if(to.name === 'personal'){
            notNext = true;
        }
    }
    if(!notNext){
        next()
    }
});

window.cookieHelper = {
    readCookie: function (name) {
        let nameEQ = name + "=";
        let ca = document.cookie.split(';');
        for (let i = 0; i < ca.length; i++) {
            let c = ca[i];
            while (c.charAt(0) === ' ') c = c.substring(1, c.length);
            if (c.indexOf(nameEQ) === 0) return c.substring(nameEQ.length, c.length);
        }
        return null;
    },
}


